body {
  margin: 0;
  padding: 10px;
  font-family: sans-serif;
  text-align: center;
  background-color: #1dbfed;
  color: white;
}

.jumbotron {
  background-color: #1dbfed;
  padding: 10px;
}

.bgbox {
  background-color: #7de3fc;
  height: 200px;
  line-height: 160px;
  padding: 10px;
  margin-bottom: 20px;
}

.bgbox h3 {
  vertical-align: middle;
  display: inline-block;
}

.clickable:hover {
  cursor: pointer;
}

.questionbox {
  background-color: #7de3fc;
  padding: 20px 0px;
  margin-bottom: 20px;
}

input[type="text"] {
  margin: 5px;
  padding: 5px;
  color: black;
  text-align: center;
}

form {
  margin: 20px;
}

button {
  margin-top: 10px;
}

.btn {
  background-color: #7de3fc;
}


